    const validateUrl = (URL) => {
    const regex = new RegExp(/(https?:\/\/.*\.(?:png|jpg|svg))/);
    return regex.test(URL);
};


export const validate = (info) => {
    let error = {};

    if(info.name.length > 0 && info.name.length < 4) error.name = 'The name must have a minimum of 5 lettersüòêüòê!!';
    if(info.name.length > 15) error.name = 'The name cannot have more than 15 lettersü§îü§î!!';
    if(info.strength <= 0) error.strength = 'Need strengthü§îü§î!!';
    if(info.defense <= 0) error.defense = 'Need defenseü§îü§î!!';
    if(info.attack <= 0) error.attack = 'Need attackü§îü§î!!';
    if(info.height <= 0) error.height = 'Need heightü§îü§î!!';
    if(info.weight <= 0) error.weight = 'Need weightü§îü§î!!';
    if(info.speed <= 0) error.speed = 'Need speedü§îü§î!!';
    if(!info.name) error.name = 'You must   put a nameüòêüòê!!';
    if(info.hp <= 0) error.hp = 'Need hpü§îü§î!!';
    if(validateUrl(info.image) && info.image !== '') error.image = 'Unsupported formatü§îü§î!!';
    return error;
}